function msag(err) {
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        alert(err.response.data.error.details);
        break;
      case 401:
        alert("");
        break;

      case 403:
        alert("");
        break;

      case 404:
        alert("");
        break;

      case 408:
        alert("");
        break;

      case 500:
        alert("");
        break;

      case 501:
        alert("");
        break;

      case 502:
        alert("");
        break;

      case 503:
        alert("");
        break;

      case 504:
        alert("");
        break;

      case 505:
        alert("HTTP");
        break;
      default:
    }
  }
}

