/**
 * @description LocalStorage ( 1M  | )
 */

type ITimeUnit = 'm' | 'min' | 'h' | 'd' | 'day' | 'M' | 'month' | 'w' | 'week' | 'y' | 'year';
const LimitSize = 1 * 1024 * 1024; // 1M utf-16)
/**
 * @description 
 * @param value {any}
 * @return type {string}
 */
const getType = (value) => {
  return Object.prototype.toString.call(value).slice(8, -1).toLowerCase();
}

/** 

 * @param key {string} key
 * @param value {any} value
 * @param expire {array}  [, ]
 */
const unitTimeStamp = {
  m: 60,
  min: 60,
  h: 60 * 60,
  d: 60 * 60 * 24,
  day: 60 * 60 * 24,
  w: 60 * 60 * 24 * 7,
  week: 60 * 60 * 24 * 7,
  M: 60 * 60 * 24 * 30,
  month: 60 * 60 * 24 * 30,
  y: 60 * 60 * 24 * 365,
  year: 60 * 60 * 24 * 365,
}
const setLocalStorageItem = (key: string, value: any, expire?: [number, ITimeUnit]) => {
  /**  */
  if (['undefined', 'string', 'null'].includes(getType(value)) && !value) {
    value = null;
  }
  let data = {};
  let timestamp = 0;
  /**  */
  if (JSON.stringify(value).length > LimitSize) {
    throw new Error('')
  }
  /**  */
  /**  */
  let time = expire?.[0];
  let unit = expire?.[1];
  const secondTime = 1000; // 1 = 1000
  if (expire && (Array.isArray(expire) ? false : Number(expire) < 0)) {
    throw new Error("eg: [1, 'day']")
  }
  /**  */
  if (time <= 0) {
    throw new Error('')
  }
  /** unitmap */
  if (unit && !unitTimeStamp[unit]) {
    throw new Error('')
  } else {
    timestamp = unitTimeStamp[unit] * secondTime;
  }
  data = {
    value, // 
    time: Date.now(), // 
    expire: timestamp, // 
  }
  localStorage.setItem(key, JSON.stringify(data))
}
/**
 * @description 
 * @param map {object}
 */
const setLocalStorageMap = (map, expire?: [number, ITimeUnit]) => {
  try {
    let keys = Object.keys(map);
    keys.forEach((key) => {
      let item = map[key];
      setLocalStorageItem(key, item, expire)
    })
  } catch (e) {
    console.error(e)
  }
}
/**
 * @description keynull
 * @param  key {string}
 * @return value {any}
 */
const getLocalStorage = (key: string) => {
  /** key */
  if (!localStorage.getItem(key) || JSON.stringify(localStorage.getItem(key)) === 'null') {
    return null;
  }

  const storageVal = JSON.parse(localStorage.getItem(key));
  let nowTime = Date.now();
  //  - null
  if (storageVal.expire && storageVal.expire < (nowTime - storageVal.time)) {
    removeLocalStorage(key);
    return null;
  } else {
    //  - value
    setLocalStorageItem(key, storageVal.value, storageVal.expire);
    return storageVal.value;
  }
}
/**
 * @description 
 * @return arr
 */
const getAllStorage = () => {
  let keys = Object.keys(localStorage); // localStorage
  let arr = []; // 
  keys.forEach((e) => {
    arr.push({key: e, val:getLocalStorage(e)});
  });
  return arr;
}
/**
 * @description 
 * @param key {string}
 */
const removeLocalStorage = (key: string) => {
  try {
    localStorage.removeItem(key);
  } catch (e) {
    console.error(e);
  }
}
/**
 * @description 
 */
const removeAllLocalStorage = () => {
  localStorage.clear()
}

export default {
  setLocalStorageMap,
  setLocalStorageItem,
  getLocalStorage,
  removeLocalStorage,
  removeAllLocalStorage,
  getAllStorage
}